import React from 'react'
import Pagination from "react-bootstrap/Pagination";

function PaginationFunction({inventoryCount, itemsPerPage, gotoPage}){

    let currentItemList = [];
    for(let number = 1; number<=Math.ceil(inventoryCount/itemsPerPage); number++ ){
        currentItemList.push(
        <Pagination.Item key={number} onClick={()=>gotoPage(number)}> 
        {number}
        </Pagination.Item>);
    }
    return(
        <div>
            <Pagination>{currentItemList}</Pagination>
        </div> 
    );
}
export default PaginationFunction;