import React from "react";
import Card from "react-bootstrap/Card";
import CardGroup from "react-bootstrap/CardGroup"
//import ShirtWhite from "./pictures/ShirtWhite.jpeg";
import PaginationFunction from "./Pagination";
import { Container, Row, Col } from "react-bootstrap"
import {connect} from 'react-redux'



function BuyerScreenFunction (props) { //{inventory, isloading, inventoryCount, itemsPerPage, gotoPage}, 
  //console.log(props)
// if(isloading) {return <h2> {isloading}Loading........</h2>} else {return <h2>we are in else condition</h2>}
  return (
    
    <Container fluid>
      {props.isloading?<Row>
       isLoading....  {props.isloading}      
      </Row>: <div> Loaded.</div> }
      <div>
      <Row>
       Welcome   {props.name}     
      </Row>
      <Row>
            
      {props.inventory.map((item, key) => (
        <Col xs={2} key={key}>        
        <Card key={key}  border="primary">
          <Card.Img variant="top" src={require(`${item.picture}`)} />
          <Card.Body>
            <Card.Title>{item.name}</Card.Title>
            <Card.Text>{item.price}</Card.Text> 
          </Card.Body>
          <Card.Body>
            <Card.Link href="#">Reviews</Card.Link>
            <Card.Link href="#">Ratings</Card.Link>
          </Card.Body>
        </Card>
        </Col>
      ))} 
        
      </Row>
      </div>
      {/* } */}
      {/* <Row><Col> */}
      {/* <PaginationFunction inventoryCount={props.inventoryCount} itemsPerPage={props.itemsPerPage} gotoPage={props.gotoPage}></PaginationFunction> */}
      {/* </Col></Row> */}
    </Container>
  );
}
//const hi=({name})=>{console.log({name})}

const mapStateToProps = (state) =>(

  {name:state.loginUser.userName}
)

export default connect(mapStateToProps)(BuyerScreenFunction);
 