import React, { Component, useRef, useState, useEffect } from "react";
import axios from 'axios'
import Button from 'react-bootstrap/Button'
import {Link, useHistory, history} from 'react-router-dom'
import {loginUser} from './actions'



function LoginScreenFunction () {

    const [users,setUsers] = useState();
    const [userType, setUserType] = useState();
    const [inventory, setinventory] = useState([]);
    const [loading, setLoading] = useState(true);
    const [currentPage, setCurrentPage] = useState(1);
    const [itemsPerPage, setItemsPerPage] = useState(7);

    const indexOfLastPage = currentPage*itemsPerPage;
    const indexOfFirstPage = indexOfLastPage-itemsPerPage;
    const currentInventory = inventory.slice(indexOfFirstPage,indexOfLastPage);


    const username = useRef();
    const password = useRef();
    const history = useHistory();
    //const{match:{history}} = props;

    function fetchUsers (){
        axios("http://localhost:4000/Users").then((res) => {
            setUsers(res.data);
          })
    }
    function fetchInventory(){
        axios("http://localhost:4000/Products").then((res) => {
        setinventory(res.data);
        setLoading(false)
      })
      .then(()=>setLoading("false"));  
    }

    useEffect(() => {
        fetchUsers(); 
        fetchInventory();
      },[]);
    
    const handleClick = (e) =>{
        e.preventDefault()
        const userCategory = users.filter(v => (v.name === username.current.value && v.password === password.current.value));
        {userCategory!=0&&(userCategory[0].category==="Buyer"||"Seller")?history.push(`/${userCategory[0].category}Screen`):history.push("/")}
        
    }
    
        return (
            <form className="text-center" >
                <h3>Sign In</h3>

                <div className="form-group">
                    <label>Email address</label>
                    <input type="email"  placeholder="Enter email" ref={username} />
                </div>

                <div className="form-group">
                    <label>Password</label>
                    <input type="password"  placeholder="Enter password" ref={password}/>
                </div>

                <div className="form-group">
                    <div className="custom-control custom-checkbox">
                        <input type="checkbox"  id="customCheck1" />
                        <label  htmlFor="customCheck1">Remember me</label>
                    </div>
                </div>

                {/* <Link to='/BuyersScreen'> */}
                    <Button onClick={(e) => handleClick(e)}>Submit</Button>
                {/* </Link> */}
                <p>
                    Forgot <a href="#">password?</a>
                </p>
            </form>
        );    
}


export default LoginScreenFunction;