import React from 'react'
import PaypalExpressBtn from 'react-paypal-express-checkout';

function CheckoutFunction()
{
    const onSucess=(payment)=>{
        console.log("Payment was succeeded", payment)
    }
    const onCancel=(data)=>{
        console.log("Payment was cancelled", data)
    }
    const onError=(error)=>{
        console.log("Payment was denied",error)
    }
    const env = 'sandbox';
    const currency = 'USD';
    const total = 100
    const client = {
        sandbox:'AbArWy5WYxJzKn1hg1tr0TXL0L6No-nH_Mfzp7miGshN_O7Lyl34WQ9i72aFPKtnIICN25HoWbe4Kjj7'
    }
    return(
        <div className="text-center">
            <PaypalExpressBtn env={env} client={client} currency={currency} total={total} onSucess={onSucess} onCancel={onCancel} onError={onError} ></PaypalExpressBtn>
        </div>
    )
}

export default CheckoutFunction;
