import React, {useState, useRef, useEffect} from 'react'
import axios from 'axios'
import Form from 'react-bootstrap/Form'
import Row from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Form'
import Button from 'react-bootstrap/Button'

function AddProductFunction(){
    const productName  = useRef();
    const productCategory  = useRef();
    
    //const [productName,setProductName] = useState("Text")
    const [isLoading, setLoading] = useState(false);
    const [fileName, setFileName] = useState();
    const [file, setFile] = useState();

    const handleClick = async(e) => {
        e.preventDefault();  //------------------------------------------------------
        setLoading(true);
        //setProductName(val)

        // const url = 'C:/AARTHI/REACT/React Workspace/shoppingapp/src';
        // const formdata = new FormData();
        // formdata.append('file',file)
        // const config = {
        //     headers: {
        //         'content-type': 'multipart/form-data'
        //     }
        // }

        await axios.post(`http://localhost:4000/Products`,{
            "name": productName.current.value,
            "category": productCategory.current.value,
            "picture": fileName,
            "price": 500,
            "Status": "Available",
            "File" : file
        })
        setLoading(false);
    }
    function handleChange(e) {
        const val = e.target.value;
        setFileName("./pictures/"+(val.substring(val.lastIndexOf("\\")+1,val.length)))
        setFile(e.target.files[0])
    //     const formdata = new FormData();
    //     formdata.append('file',file)
    //     axios.post("http://localhost:4000/upload", formdata, { // receive two parameter endpoint url ,form data 
    //   })
    //   .then(res => { // then print response status
    //     console.log(res.statusText)
    //   })
    } 
    useEffect(()=>console.log(file))
    return(      
        <Form >
            <Form.Group controlId="exampleForm.ControlInput1">
                <Form.Row>
                    <Form.Label column="md" md={2}>Product Name</Form.Label>
                    <Col><Form.Control size="md" type="text" placeholder="Enter Product name" ref={productName}></Form.Control></Col>
                </Form.Row>                
            </Form.Group>

            <Form.Group controlId="exampleForm.ControlInput2">
                <Form.Row>
                    <Form.Label column="md" md={2}>Product Category</Form.Label>
                    <Col><Form.Control size="md" as="select" placeholder="Select a Category" ref={productCategory}>
                    <option>Clothing</option>
                    <option>Shoes</option>
                    <option>Phones</option>
                    <option>Others</option>    
                    </Form.Control></Col>
                </Form.Row>                
            </Form.Group>

            <Form.Group controlId="exampleForm.ControlForm1">
                <Form.Row>
                    <Form.Label column="md" md={2} >Product Photo</Form.Label>
                    <Col><Form.File size="md" placeholder="Upload a Photo" onChange = {handleChange} ></Form.File></Col>
                </Form.Row>                
            </Form.Group>

            <Form.Row>
                <Button variant="primary" type="submit" onClick={handleClick}>{isLoading?'Submitting...':'Click To Upload'}</Button>
            </Form.Row>
        </Form>   
    )
}

export default AddProductFunction;